.where-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: flex-end;
  padding-top: 43px;
  padding-bottom: 145px;

  &__item {
    width: 57%;

    @include small-desktop {
      width: 66%;
    }
  }

  @include small-desktop {
    padding: 0 0 125px 0;
  }
}
