.arrow-link {
  font-size: 24px;
  line-height: 170%;
  /* identical to box height, or 41px */

  letter-spacing: 0.01em;
  text-transform: uppercase;

  color: #F7F7F8;
  display: block;
padding: 70px 0;
  text-decoration: none;
  display: flex;
  justify-content: space-between;
  width: 100%;
  border-top: 1px solid rgba(247, 247, 248, 0.4);
  border-bottom: 1px solid rgba(247, 247, 248, 0.4);

  & svg {

    @include small-desktop {
      width: 24px;
      height: 24px;
    }  
  }

  @include small-desktop {
    font-size: 18px;
    line-height: 30px;
    padding: 64px 0;

  }

  @include big-laptop {
    font-size: 16px;
    line-height: 27px;
    padding: 60px 0;
  }
}
