.where-header {
  position: relative;
  min-height: 500px;

  &__text {
    position: absolute;
    font-size: 10vw;
    line-height: 120%;
    font-weight: 400;
    letter-spacing: -0.06em;
    color: #F7F7F8;
    text-transform: uppercase;
    margin: 0;

    &--first {
      top: 50px;
      right: 13%;
    }

    &--second {
      top: 165px;
      left: 5.5%;
    }
  }

  @include small-desktop {
    &__text {

      &--first {
        right: 11%;
      }

      &--second {
        top: 160px;
        left: 0;
      }
    }
  }

  @include big-laptop {

    min-height: 455px;

    &__text {

      &--first {
        right: 11%;
      }

      &--second {
        top: 145px;
        left: 0;
      }

    }
  }
}
