.bottom {
  display: flex;
  justify-content: space-between;
  padding-bottom: 103px;


  &__about {
    text-transform: uppercase;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 40px;
    color: #F7F7F8;
    text-decoration: none;
    position: relative;
    transition: 0.5s;

    &:hover {
      opacity: 0.7;
      transition: 0.5s;
      //cursor: pointer;
    }

    @include small-desktop {
      font-size: 18px;
      line-height: 30px;
    }

    @include big-laptop {
      font-size: 16px;
      line-height: 27px;
    }
  }

  &__social {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;

    & a {
      font-size: 9.26756px;
      line-height: 11px;
      border-radius: 50%;
      padding-top: 7px;
      padding-bottom: 7px;
      color: #211D1E;
      background: #FFFFFF;
      display: block;
      width: 24px;
      height: 24px;
      box-sizing: border-box;
      text-align: center;
    }

    @include small-desktop {
      margin-left: 30px;
    }

    @include big-laptop {
      margin-left: 50px;
    }
  }

  &__social-item {
    margin-right: 10px;

    &:last-child {
      margin-right: 0;
    }

    
    @include small-desktop {
      margin-right: 5px;
    }
  }

  &__language {
    color: #F7F7F8;
  }

  &__language-item {
    opacity: 0.5;
    position: relative;
    margin-right: 14px;
    font-size: 24px;
    line-height: 40px;

    &--active {
      opacity: 1;
    }

    &::after {
      content: "|";
      position: absolute;
      right: -12px;
    }

    &:last-child {
      margin-right: 0;

      &::after {
        content: none;
      }
    }

    @include small-desktop {
      font-size: 18px;
      line-height: 30px;
    }

    @include big-laptop {
      font-size: 16px;
      line-height: 27px;
    }
  }

  &__behance {
    text-transform: uppercase;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 40px;
    color: #F7F7F8;
    text-decoration: none;
    position: relative;
    transition: 0.5s;

    display: flex;

    &:hover {
      opacity: 0.7;
      transition: 0.5s;
      //cursor: pointer;
    }

    & span {
      margin-right: 5px;
    }

    & svg {
      margin-top: 5px;
    }

    @include small-desktop {
      font-size: 18px;
      line-height: 30px;
      margin-right: 20px;
    }

    @include big-laptop {
      font-size: 16px;
      line-height: 27px;
      margin-right: 30px;
    }
  }

  &__center {
    display: flex;
    justify-content: space-between;
    width: 400px;
    margin-left: -10%;

  }

  @include small-desktop {
    padding-bottom: 50px;
  }
}
