.top {
  padding-top: 31px;
  display: flex;
  justify-content: space-between;


  &__logo {
    transition: 0.5s;

    &:hover {
      opacity: 0.7;
      transition: 0.5s;
      //cursor: pointer;
    }
  }

  &__menu-btn {
    display: flex;
    padding-top: 5px;
    text-transform: uppercase;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 30px;
    color: #F7F7F8;
    text-decoration: none;
    position: relative;
    transition: 0.5s;

    &:hover {
      opacity: 0.7;
      transition: 0.5s;
      //cursor: pointer;
    }
     & span {
       margin-left: 10px;
     }

     & svg {
       margin-top: 5px;
     }

     @include big-laptop {
      font-size: 16px;
      line-height: 28px;
    }
  }
}

@include small-desktop {
  .top {
    padding-top: 35px;
  }
}
