.main__wrapper {
  // cursor: none;
  max-width: 1920px;
  margin: 0 auto;
  height: 100%;
  padding: 0 64px;
  display: flex;
  flex-direction: column;
  position: relative;

  @include small-desktop {
    padding: 0 100px;
  }

  @include big-laptop {
    padding: 0 76px;
  }
}

.main__small-menu {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 600px;
  left: 75px;

  @include small-desktop {
    top: 562px;
    left: 100px; 
  }

  @include big-laptop {
    top: 522px;
    left: 76px; 
  }
}

.main__small-menu-item {
  opacity: 0.5;
  &--active {
    opacity: 1;
  }

  & a {
    text-transform: uppercase;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 29px;
    color: #F7F7F8;
    text-decoration: none;
    position: relative;
    transition: 0.5s;

    &:hover {
      opacity: 0.7;
      transition: 0.5s;
      //cursor: pointer;
    }

    @include small-desktop {
      font-size: 18px;
      line-height: 26px;
     }

     @include big-laptop {
      font-size: 16px;
      line-height: 22px;
    }
  }
}

@keyframes moveCursor1 {
    from {
        transform: scale(1);
    }

    to {
        transform: scale(.8);
    }
}

@keyframes moveCursor2 {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(2);
    }

    100% {
        transform: scale(1);
        opacity: 0;
    }
}
